@charset "UTF-8";
/* --------------------------------------------------
#   Bookmark Card
-------------------------------------------------- */
.kg-bookmark-card {
  position: relative; }

.kg-bookmark-publisher {
  position: relative; }

.kg-bookmark-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row-reverse;
  color: currentColor;
  background: #ffffff;
  font-family: "Inter ui", sans-serif;
  text-decoration: none;
  border-radius: 12px;
  overflow: hidden; }

.content a.kg-bookmark-container {
  color: #000000;
  font-weight: 400;
  text-decoration: none; }
  .content a.kg-bookmark-container:hover {
    color: #000000; }
    .content a.kg-bookmark-container:hover .kg-bookmark-title {
      color: #5e57e3; }

.kg-bookmark-content {
  flex-basis: 0;
  flex-grow: 999;
  padding: 20px;
  order: 1; }

.kg-bookmark-title {
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.3em; }

.kg-bookmark-description {
  display: -webkit-box;
  margin: 0.5em 0 0 0;
  overflow: hidden;
  opacity: 0.8;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical; }

.kg-bookmark-metadata {
  display: flex;
  align-items: center;
  font-size: 1rem;
  font-weight: 400;
  margin-top: 1.25rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.kg-bookmark-icon {
  display: inline-block;
  height: 1rem;
  margin-right: 0.6rem;
  width: 1rem; }

.kg-bookmark-thumbnail {
  display: block;
  flex-basis: 16rem;
  flex-grow: 1;
  justify-content: flex-end; }
  .kg-bookmark-thumbnail img {
    border-radius: 12px;
    max-width: 100%;
    height: auto;
    min-height: 200px;
    vertical-align: bottom;
    object-fit: cover; }

.kg-card img {
  border-radius: 0; }

.kg-card .kg-bookmark-thumbnail img {
  border-radius: 0; }

.kg-bookmark-author {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden; }

.kg-image-card img,
.kg-gallery-image img {
  height: auto;
  max-width: 100%;
  margin: 0 auto; }

.kg-width-full img {
  border-radius: 0;
  margin: 0.3rem auto; }

.kg-bookmark-author:after {
  content: "•";
  margin: 0 6px; }

figcaption {
  padding: 0.5rem;
  text-align: center; }

/* --------------------------------------------------
#   Image Gallery Card
-------------------------------------------------- */
.kg-width-full .kg-image {
  max-width: 100%; }

.kg-width-wide {
  max-width: 90%;
  margin: 0 auto; }

.kg-gallery-card + .kg-gallery-card {
  margin-top: 0.75em; }

.kg-gallery-container {
  position: relative; }

.kg-gallery-row {
  display: flex;
  flex-direction: row;
  justify-content: center; }

.kg-gallery-image img {
  display: block;
  margin: 0;
  width: 100%;
  height: 100%; }

.kg-gallery-row:not(:first-of-type) {
  margin: 0.75em 0 0 0; }

.kg-gallery-image:not(:first-of-type) {
  margin: 0 0 0 0.75em; }

