  @media (prefers-color-scheme: darkdisabled) {
    .post-comments {
      border-color: #3b3d40; } }
  .post-comments.activated {
    background: #f7f8fa;
    border: 1px solid #e1e3e6; }
  @media (prefers-color-scheme: darkdisabled) {
    .post-comments.activated {
      background: #222426;
      border-color: #3b3d40; } }
  .post-comments p {
    font-size: 1.2em;
    line-height: 1.3em; }
  .post-comments h2 {
    font-size: 2em; }
  .post-comments h2 a {
    text-decoration: none; }
  .post-comments-activate {
    display: inline-block;
    height: 4rem;
    margin: 2rem 0;
    padding: 0 2rem;
    border: 1px solid #c4c7cc;
    font-weight: 500;
    font-size: 1.6em;
    line-height: 4rem;
    color: #73777d;
    text-align: center;
    cursor: pointer;
    transition: all ease 0.2s;
    border-radius: 2em; }
  @media (prefers-color-scheme: darkdisabled) {
    .post-comments-activate {
      border-color: #909499;
      color: #c4c7cc; } }
  .post-comments-activate:focus, .post-comments-activate:hover {
    color: var(--color-primary-active);
    border-color: var(--color-primary-active); }
  .activated .post-comments-activate {
    display: none; } }
